import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"

import { CardDeck, Card } from "react-bootstrap" 
import cs101 from '../images/cs101.png';


const CoursesPage = () => (
  <Layout>
    <SEO title="Courses" />
    <div className="container">
	    <h1>Courses</h1>
	    <p>Welcome to page 2</p>

	    <CardDeck>

	      <Card>
		    <img src={cs101} />
		    <Card.Body>
		      <Card.Title>CS101</Card.Title>
		      <Card.Text>
		        Intro to Computer Science | Spring 2020
		      </Card.Text>
		    </Card.Body>
		    <Card.Footer>
		      <small className="text-muted">Last updated 3 mins ago</small>
		      <Link className="nav-link" to="./cs101/">CS101</Link>
		    </Card.Footer>
		  </Card>


		{/**  
		  <Card>
		    <Image /> 
		    <Card.Body>
		      <Card.Title>ML101</Card.Title>
		      <Card.Text>
		        Machine Learning | Fall 2019
		      </Card.Text>
		    </Card.Body>
		    <Card.Footer>
		      <small className="text-muted">Last updated 3 mins ago</small>
		      <Link className="nav-link" to="./ml101/">ML101</Link>
		    </Card.Footer>
		  </Card>
		**/}

		</CardDeck>
	</div>
  </Layout>
)

export default CoursesPage
